import React, { useEffect, useState } from "react";
const Web3 = require('web3');
import {useTranslation} from 'react-i18next';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DateTimePicker } from '@mui/x-date-pickers/DateTimePicker';

import { Grid,Button,TextField,InputLabel,FormControl,Select,MenuItem,Switch,Stack } from '@mui/material';
import { Delete } from '@mui/icons-material';
import { DataGrid } from '@mui/x-data-grid';

import SetRPC from  'components/common/SetRPC'
import ImportPrivateKey from  'components/common/ImportPrivateKey'
import Snackbars from 'components/Snackbars'
import CONFIG from 'config/config.json'
import { sendTxAction } from 'helper/SendTxAction'
import MainCard from 'ui-component/cards/MainCard'

import { useTheme } from '@mui/material/styles';

let sOpen = false

function MintBy0xData() {
  const {t} = useTranslation()
  const theme = useTheme()
  const {CHAIN,CHAINID} = CONFIG.NETWORK
  const [rpc, setRPC] = useState(CONFIG.NETWORK.RPC); 
  const [web3, setWeb3] = useState(new Web3(new Web3.providers.HttpProvider(rpc)))
  //0xe03a9f2f0d1dae943c2eeb5c93c95cbbf524ad7d
  const [contractAddress, setContractAddress] = useState("");
  const [maxPriorityFeePerGas, setMaxPriorityFeePerGas] = useState(2);
  const [maxFeePerGas, setMaxFeePerGas] = useState(2);
  const [ethValue, setEthValue] = useState(0);
  const [paramArray, setParamArray] = useState([
    {
      type:"uint256",
      value: 1
    }
  ]);  
  const [privateKey, setPrivateKey] = useState({
    addrStr:"",   
    pkStr:"",
    pkArr:[],
    addrArr:[] 
  });   

  const [snackbarsStatu, setSnackbarsStatu] = useState({
    isOpen: false,
    msg: ''
  });

  const changeRPCAction = (newRpc) => {
    if(newRpc.available){     
      setWeb3(new Web3(new Web3.providers.HttpProvider(newRpc.rpc)))      
    }     
  };

  const setContractAddressAction = (event) => {
    setContractAddress(event.target.value);
  };

  const setMaxPriorityFeePerGasAction = (event) => {
    setMaxPriorityFeePerGas(event.target.value);
  }; 
  
  const setMaxFeePerGasAction = (event) => {
    setMaxFeePerGas(event.target.value);
  };
  
  const setEthValueAction = (event) => {
    setEthValue(event.target.value);
  }; 
  
  const setMintMethodAction = (event) => {
    setMintMethod(event.target.value);
  };  
  
  const setPrivateKeyAction = (privateKey) => { 
    setPrivateKey(privateKey)
    setGridData(privateKey.pkArr);
  }; 

  const paramSelectChange = (e) => {
    let index = e.target.name.slice(2)
    let value = e.target.value
    paramArray[index].type = value
    //////console.log("paramArray==>",paramArray)
    setParamArray(paramArray)
    parseParamsArray()
  };
  
  const paramChange = (e) => {
    let index = e.target.id.slice(2)
    let value = e.target.value
    paramArray[index].value = value    
    setParamArray(paramArray)
    parseParamsArray()
  };

  const startBatMint =( async ()=>{ 
      ////console.log('verify',verifyForm())
      if(!verifyForm()) return
      ////console.log('sOpen0',sOpen)
      //sOpen要写成全局的sOpen变化了interval内部才能获取到
      if(sOpen){//定时
        ////console.log('sOpen1',sOpen)
        ////console.log(new Date(dateValue).getTime())
        let excuteInterval = setInterval(() => {
          let leftTime = (new Date(dateValue).getTime() - Date.now())/1000  
          leftTime = leftTime > 0 ? leftTime : 0
          setLeftTime(leftTime)
          ////console.log('sOpen2',sOpen)                   
          if(sOpen == false){
            clearInterval(excuteInterval)
            return;
          }  
          ////console.log(leftTime)     
          if( leftTime == 0){
            ////console.log('00000000000000')            
            clearInterval(excuteInterval)
            startInterval()       
          }            
        }, 100);              
      }else{        
        startInterval()        
      }
  })

  //十六进制数据mint和普通mint有两个区别，1、预判gas 2、数据编码
  const startInterval = () => {
    let mintCheckInterval = setInterval(async ()=>{
      const normalGas = await getNormalGas(privateKey.pkArr[0])
      // ////console.log('normalGas',normalGas)
      // ////console.log('typeof normalGas',typeof normalGas) 
      // ////console.log('typeof normalGas == number',typeof normalGas == 'number') 
      // ////console.log('typeof normalGas == number estimateGas < CONFIG.gaslimit',typeof normalGas == 'number' && normalGas < CONFIG.gaslimit)                  
      ////console.log("normalGas",normalGas)
      if(normalGas.error == 'INSUFFICIENT_FUNDS'){
        clearInterval(mintCheckInterval);
        return;
      }
      if (typeof normalGas == 'number' && normalGas < CONFIG.GASLIMIT){
        clearInterval(mintCheckInterval)
        privateKey.pkArr.forEach((pk,index)=>{
          updateGridRows({ index: index, timesChange: 1, status: "pending..." })  
          mint(pk,index)
        }) 
      }
    },1000)
  }

  const updateGridRows = async ({ index, timesChange, status }) => {
    try {
      setGridRows((prev) => {
        // https://stackoverflow.com/questions/64957735/
        // typeerror-cannot-assign-to-read-only-property-0-of-object-object-array-in
        // Try creating a copy of the array before trying to sort it. Like using a spread operator.
        let tmpGridRows = [...prev]      
        for (let i = 0; i < tmpGridRows.length; i++) {
          if (i == index) {
              tmpGridRows[i] = { 
              id: tmpGridRows[i].id,
              address: tmpGridRows[i].address,
              balance: tmpGridRows[i].balance,
              times: tmpGridRows[i].times + timesChange, 
              status: status 
            }
          }
        } 
        return tmpGridRows     
      })
    } catch (error) {
      ////console.log('setGridRows error ==> ', error)       
    }
    setSnackbarsStatu({ isOpen: true, msg: status });
    return
    // let tmpGridRows = JSON.parse(JSON.stringify(gridRows))
    // ////console.log('tmpGridRows1',tmpGridRows)        
  }

  //const Mint = (signData) => () => {
    //setSnackPack((prev) => [...prev, { message, key: new Date().getTime() }]);
  //};

  async function mint(pk,index) {
    const { _sendTx} = sendTxAction(web3, CHAIN)
    const data = transaction0xData    
    const from = web3.eth.accounts.privateKeyToAccount(pk).address
    let nonce = await web3.eth.getTransactionCount(from);
    const response = await _sendTx({
      from: from, 
      to: contractAddress, 
      value: ethValue, //Unit is eth
      gasLimit: CONFIG.GASLIMIT,
      maxPriorityFeePerGas: maxPriorityFeePerGas, //Unit is Gwei
      maxFeePerGas: maxFeePerGas, //Unit is Gwei
      data: data,
      nonce: nonce,
      pk: pk
    })

    let res = '' 
    ////console.log('response==>',response)
    ////console.log('Object.keys(response)',Object.keys(response))
    //检测是否有error属性
    if (Object.keys(response).includes('error')) {
      res = '× ' + response.error
    }
    else {
      res = '✓ ' + response.transactionHash
    }
    ////console.log('res', res)

    updateGridRows({ index: index, timesChange: 0, status: res })
    ////console.log('response==>', response)
  }

  //estimateGas async返回值是一个promise对象 await命令后面跟着的是一个promise对象
  async function getNormalGas(pk){
    const { _estimateGas } = sendTxAction(web3, CHAIN)
    const accountObject = web3.eth.accounts.privateKeyToAccount(pk)
    const from = accountObject.address;

    const nonce = await web3.eth.getTransactionCount(from)
    const normalGas = await _estimateGas({
      from: from,
      to: contractAddress,
      value: ethValue, //Unit is eth
      gasLimit: CONFIG.GASLIMIT,
      maxPriorityFeePerGas: maxPriorityFeePerGas, //Unit is Gwei
      maxFeePerGas: maxFeePerGas, //Unit is Gwei
      data: transaction0xData,
      nonce: nonce
    })    
    return normalGas   
  }  

  //Mint Functions end

  const [paramsDOMArray, setParamsDOMArray] = useState([])  
  const parseParamsArray = (()=>{
    let params = []
    //////console.log("paramArray==>",paramArray)
    paramArray.forEach((element,index) => {
      //////console.log('element:',element);
      params.push(
        <Grid item key={index} xs={12}>
        <Grid container spacing={2}>
          <Grid item xs={2}>
          <FormControl fullWidth>
            <InputLabel id={"param_label"+ index} size="small">Param Type</InputLabel>
            <Select
              id={"param_select_"+ index}
              name = {"S_"+ index}
              label="Param Type"
              labelId={"param_label"+ index}
              size="small" 
              value={paramArray[index].type}              
              onChange={paramSelectChange}
            >
              <MenuItem value={"address"}>address</MenuItem>
              <MenuItem value={"uint8"}>uint8</MenuItem>
              <MenuItem value={"uint256"}>uint256</MenuItem>
              <MenuItem value={"bool"}>bool</MenuItem>              
            </Select>           
          </FormControl>
          </Grid> 
          <Grid item xs={6}>
            <TextField 
              fullWidth              
              id={"T_"+ index}
              label="Param Value" 
              variant="outlined"  
              size="small" 
              value ={paramArray[index].value}
              onChange ={paramChange}
            />
          </Grid> 
          <Grid item xs={2}>
          <Button id={index} variant="outlined" startIcon={<Delete />} onClick={deleteParam}>Del</Button>
          </Grid>
        </Grid>
      </Grid>
      )
    })
    //////console.log("params//////////",params)
    setParamsDOMArray(params)
  })

  const deleteParam = ((e)=>{
    //////console.log("e.id",e.target.id)
    paramArray.splice(e.target.id,1);
    setParamArray(paramArray)
    parseParamsArray();     
  })  

  useEffect(() => {
    parseParamsArray();
  }, []);

  const [gridRows,setGridRows] = useState([    
    { id: 1, address: "", balance: "", times: "", hash: "No Data", status: "", action: ""},
    ])

  const columns = [
    { field: "id", hide: true },
    { field: "address", headerName: t('mintBy0xData.address'), width: 150 },
    { field: "balance", headerName: t('mintBy0xData.amount'), width: 80 },
    { field: "times", headerName: t('mintBy0xData.requestTimes'), width: 160 },
    { field: "status", headerName: t('mintBy0xData.status'), width: 400 }        
  ];

  const setGridData = async (pkArr)=>{
    let rows = await Promise.all(
      pkArr.map(async (item,index) => {
        const address = web3.eth.accounts.privateKeyToAccount(item).address
        const balance = await web3.eth.getBalance(address)
        return {
          id: index + 1,
          address: address,
          balance: web3.utils.fromWei(balance, 'ether'),
          times: 0,
          status: "--"
        };
      })
    )
    ////console.log(rows)
    setGridRows(rows)
    ////console.log(gridRows)
  }

  const [dateValue, setDate] = useState(new Date());
  const dateChange = (newValue) => {
    setDate(newValue);
    //////console.log(dateValue)
  };

  const [switchOpen, setSwitch] = useState(false);
  const [leftTime, setLeftTime] = useState(0);    
  const  switchChange = (event) => {
    ////console.log("event.target.checked",event.target.checked)
    setSwitch(event.target.checked);
    sOpen = event.target.checked;
  };


  //mintBy0xData
  //0xb9c61cd311b47e7f9341c068754347246c895b10155f01c553862ebd28c6bacc
  const [transactionHash,setTransactionHash] = useState('')
  const [transaction0xData,setTransaction0xData] = useState('')
  const [transactionInputs,setTransactionInputs] = useState([])
  const [editButtonStr, setEditButtonStr] = useState(t('mintBy0xData.editHexData'));

  const setTransactionHashAction = (event)=> {
    ////console.log(event.target.value)
    setTransactionHash(event.target.value);
    const pattern = /0[xX]{1}[ABCDEFabcdef0-9]{64}/g;  
    if(pattern.test(event.target.value)){
      getTransactionReceipt(event.target.value)
    }
  }

  const clearHashAction = () =>{
    setTransactionHash('');
  }

  const clearContractAddressAction = () =>{
    setContractAddress('');
  }
  
  //可以没有
  const setTransaction0xDataAction = (event) =>{
    setTransaction0xData(event.target.value);    
  }  

  
  const getTransactionReceipt = async (tx)=> {
    let transactionReceipt = await web3.eth.getTransaction(tx)
    // let transactionDetail = await web3.eth.getTransactionReceipt(tx)
    // ////console.log('getTransactionDetail',transactionDetail)
    ////console.log('transactionReceipt:',transactionReceipt)
    transactionReceipt.from && parseTransactionReceipt(transactionReceipt)
  } 
 
  const parseTransactionReceipt = (receipt) => {
    const {to,maxPriorityFeePerGas,maxFeePerGas,value,gas,gasprice,input} = receipt
    setContractAddress(to)
    setMaxPriorityFeePerGas(maxPriorityFeePerGas ? web3.utils.fromWei(maxPriorityFeePerGas,'gwei') : 0)
    setMaxFeePerGas(maxFeePerGas ? web3.utils.fromWei(maxFeePerGas,'gwei') : 0)
    setEthValue(web3.utils.fromWei(value)) 
    setTransaction0xData(input) 
    const txInputs = parseInput(input) 
    setTransactionInputs(txInputs)
    // const [maxPriorityFeePerGas, setMaxPriorityFeePerGas] = useState(2);
    // const [maxFeePerGas, setMaxFeePerGas] = useState(50);
    // const [ethValue, setEthValue] = useState(0.02);
    // const [mintMethod, setMintMethod] = useState("mint");    
  }

  //拆分十六进制数据
  const parseInput = (input) =>{
    let inputs = []
    const inputLen = input.length
    let slicePos = 0  //风格开始位置  
    let sliceLen = 10 //分割长度
    let paramNum = 0 //第几个分割

    inputs[0]= input.slice(0,10) //从第一个到第十个   

    slicePos += 10
    sliceLen = 64
    paramNum ++

    while(slicePos < inputLen){
      inputs[paramNum]= input.slice(slicePos,slicePos + sliceLen)
      slicePos = slicePos + sliceLen
      paramNum ++
    }

    //////console.log('inputs',inputs)

    return inputs;
  }   

  //表单修改十六进制数据行为
  const edit0xData = () =>{
    setEditButtonStr(editButtonStr == t('mintBy0xData.editHexData') ? t('mintBy0xData.finishEditHexData') : t('mintBy0xData.editHexData'));
    if(editButtonStr == t('mintBy0xData.editHexData')) {
      const txInputs = parseInput(transaction0xData)
      //////console.log('inputs2',txInputs)    
      setTransactionInputs(txInputs)
      //////console.log('transactionInputs',transactionInputs)    
      parseHexDataParamsArray() 
      setEditButtonStr(t('mintBy0xData.finishEditHexData'));      
    }else{
      setHexDataDOMArray([])      
      setEditButtonStr(t('mintBy0xData.editHexData')); 
    }      
  }

  const [hexDataDOMArray, setHexDataDOMArray] = useState([]) 
  const parseHexDataParamsArray = (()=>{
    let params = []
    //////console.log("hexDataParamsArray==>",transactionInputs)
    transactionInputs.forEach((element,index) => {
      //////console.log('element:',element);
      params.push(
        <Grid item key={index} xs={12}>
        <Grid container spacing={2}> 
          <Grid item xs={10}>
            <TextField 
              fullWidth              
              id={"D_"+ index}
              label={t('mintBy0xData.hexDataParam')} 
              variant="outlined"  
              size="small" 
              value ={transactionInputs[index]}
              onChange ={transactionInputsChange}
            />
          </Grid> 
        </Grid>
      </Grid>
      )
    })
    //////console.log("params//////////",params)
    setHexDataDOMArray(params)
  })

  const transactionInputsChange = (e) => {
    let index = e.target.id.slice(2)
    let value = e.target.value
    transactionInputs[index] = value    
    setTransactionInputs(transactionInputs)
    setTransaction0xData(transactionInputs.join(''))
    parseHexDataParamsArray()
  };

  //================================================================VerifyForm Data
  const [formErrors, setFormErrors] = useState({})  

  const setFormErrorsAction = (id,value) =>{
    setFormErrors((prev) => {
      let newValue = { ...prev, ...{[id]:value} }
      ////console.log('newValue',newValue)
      return newValue
    })
  }

  const verifyForm = () => {
    const checkAddress = isValidAddress(contractAddress)
    setFormErrorsAction('contractAddress',!checkAddress)
    const checkEthValue = isFinite(ethValue)    
    setFormErrorsAction('ethValue',!checkEthValue)
    const checkPrivateKey = hasPrivateKey() 
    setFormErrorsAction('privateKey',!checkPrivateKey)  
    const checkTransactionHash = isValidTransactionHash(transactionHash) 
    setFormErrorsAction('transactionHash',!checkTransactionHash)          
    if(!checkAddress 
      || !checkEthValue  
      || !checkPrivateKey 
      || !checkTransactionHash
    ) 
    {return false}
    return true    
  }

  const isValidAddress = (address) => {
    if(address == '') return false
    return web3.utils.isAddress(address)
  }  

  const hasPrivateKey = () => {
    return privateKey.pkArr.length > 0
  }  

  const isValidTransactionHash = (tx)=> {
    const pattern = /0[xX]{1}[ABCDEFabcdef0-9]{64}/g  
    return pattern.test(tx)
  }

  //================================================================

  return (  
    <MainCard 
      title={t('mintBy0xData.title')}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Grid container spacing={2}>
              <Grid item xs={12}>
                <SetRPC action={{'changeRPCAction':changeRPCAction}} data ={{'rpc':rpc}}/>  
              </Grid>             
          </Grid> 
        </Grid> 

        <Grid item xs={12}>
          <Grid container spacing={2}>
            <Grid item xs={9} sm ={6}>
              <TextField 
                error = {formErrors.transactionHash} 
                fullWidth
                id="hash" 
                label={t('mintBy0xData.hash')}
                variant="outlined"  
                size="small" 
                value={transactionHash}
                onChange={setTransactionHashAction}              
              />
            </Grid>
            <Grid item xs={1}>
              <Button  variant="text" onClick={clearHashAction}>{t('mintBy0xData.hashClear')}</Button>
            </Grid>          
          </Grid>
        </Grid> 

        <Grid item xs={12}>
          <Grid container spacing={2}>
            <Grid item  xs={9} sm ={6}>
              <TextField 
                error = {formErrors.contractAddress}
                fullWidth
                id="contract_address" 
                label={t('mintBy0xData.contractAddress')} 
                variant="outlined"  
                size="small" 
                value={contractAddress}
                onChange={setContractAddressAction}              
              />
            </Grid>
            <Grid item xs={1}>
              <Button  variant="text" onClick={clearContractAddressAction}>{t('mintBy0xData.contractAddressClear')}</Button>
            </Grid>             
          </Grid>
        </Grid>                 

        <Grid item xs={12}>
          <Grid container spacing={2}>
            <Grid item   xs={12} sm ={3}>
              <TextField 
                fullWidth
                id="maxpriorityfeepergas" 
                label={t('mintBy0xData.maxpriorityfeepergas')}
                variant="outlined"  
                size="small" 
                type="number"
                value={maxPriorityFeePerGas}
                onChange={setMaxPriorityFeePerGasAction}              
              />
            </Grid>
            <Grid item  xs={12} sm ={3}>
              <TextField 
              fullWidth
              id="maxfeepergas" 
              label={t('mintBy0xData.maxFee')}
              variant="outlined"  
              size="small" 
              type="number"
              value={maxFeePerGas}
              onChange={setMaxFeePerGasAction} 
              />
            </Grid>
            <Grid item  xs={12} sm ={3}>
              <TextField 
                error = {formErrors.ethValue}
                fullWidth
                id="eth_value" 
                label={t('mintBy0xData.ethAmout')}
                variant="outlined"  
                size="small" 
                value={ethValue}
                onChange={setEthValueAction}
                />
            </Grid>
          </Grid>
        </Grid> 

        <Grid item xs={12}>
          <Grid container spacing={1}>
            <Grid item xs={10} sm ={8}>          
              <TextField 
                fullWidth
                id="0xData" 
                label={t('mintBy0xData.hexData')} 
                variant="outlined"  
                size="small" 
                multiline
                rows={4}
                value={transaction0xData}
                onChange={setTransaction0xDataAction}              
              />
            </Grid>
            <Grid item xs={1}>
              <Button  variant="text" onClick={edit0xData}>{editButtonStr}</Button>
            </Grid> 
          </Grid>
        </Grid> 

        {hexDataDOMArray}

        <Grid item xs={12}>
        <ImportPrivateKey 
          setPk={setPrivateKeyAction} 
          provider={web3} 
          chain={CHAIN} 
          title={t('mintBy0xData.importPrivateKeyTitle')}
          error = {formErrors.privateKey} 
        />
        </Grid>

        <Grid item xs={12} key={snackbarsStatu.msg} style={{ display: (snackbarsStatu.isOpen ? ' ' : 'none') }}>
          <Snackbars msg={snackbarsStatu.msg}></Snackbars>
        </Grid> 

        <Grid item xs={12}>
          <Grid container spacing={2}>            
            <Grid item xs={3}>
            {t('mintBy0xData.openTimer')}
            <Switch    
              checked={switchOpen}
              onChange={switchChange} 
            />
            </Grid> 
            </Grid>
        </Grid> 

        <Grid 
          item 
          xs={12} 
          sx={{ 
            fontsize: 16, 
            display: (switchOpen ? "flex" : "none"),
            mt:1
          }}  
          >
          <Grid 
            container 
            spacing={2}
            sx={{  
              justifyContent:'flex-start',
              alignItems:'center',
              border:'solid 1px',
              borderColor: theme.palette.grey[500],
              borderRadius:2,
              pb:'16px',
              ml:0,
              mr:0            
            }}          
          >
            <Grid 
              item
              sx={{
                minWidth:120,
                display:'flex'                
              }}
            >
              {t('mintBy0xData.leftTime')} 
              <span style={{
                  color: theme.palette.secondary.main,
                  paddingLeft:5
                }}>
                {leftTime}
              </span>        
            </Grid>
            <Grid item>
              <LocalizationProvider dateAdapter={AdapterDateFns} >
                <Stack spacing={3}>
                  <DateTimePicker
                    size="small"
                    hideTabs
                    value={dateValue}
                    onChange={dateChange}
                    minDate={new Date('2022-05-1')}
                    renderInput={(params) => <TextField {...params} />}
                  />
                </Stack>
              </LocalizationProvider>
            </Grid>             
          </Grid>
        </Grid> 

        <Grid item xs={12}>
          <Grid container spacing={2}>
            <Grid item xs={1}>
              <Button 
                variant="contained" 
                onClick={startBatMint}
              >
                {t('mintBy0xData.execute')}
              </Button>
            </Grid>          
            </Grid>
        </Grid> 

        <Grid item xs={12}  style={{ height: 300, width: "100%" }}>
          <DataGrid rows={gridRows} columns={columns} />
        </Grid>

      </Grid> 
    </MainCard>    
  );
}

export default MintBy0xData;