import React, {  useState } from "react";
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

export default function PkGroup(props) {
  const [groupNumber, setGroupNumber] = useState(1);  

  //button action
  const setGroupNumberAction = (event) => {
    const val = event.target.value
    setGroupNumber(val);
    props.data.setGroupAction(val)
  }; 

  return (
    <Box component="span" sx={{ p: 2, border: '0px dashed grey' }}>
      <Button onClick={setGroupNumberAction} value = '1' variant="outlined" size="small"  sx={{ px: 0, mr: 1, border: groupNumber == 1 ? '1px dashed #2196f3' : '1px dashed grey' }}>1</Button>
      <Button onClick={setGroupNumberAction} value = '2'  variant="outlined" size="small"  sx={{ px: 0, mr: 1, border: groupNumber == 2 ? '1px dashed #2196f3' : '1px dashed grey' }} >2</Button>
      <Button onClick={setGroupNumberAction} value = '3' variant="outlined" size="small"  sx={{ px: 0, mr: 1, border: groupNumber == 3 ? '1px dashed #2196f3' : '1px dashed grey' }}>3</Button>
      <Button onClick={setGroupNumberAction} value = '4'  variant="outlined" size="small"  sx={{ px: 0, mr: 1, border: groupNumber == 4 ? '1px dashed #2196f3' : '1px dashed grey' }} >4</Button>
      <Button onClick={setGroupNumberAction} value = '5' variant="outlined" size="small"  sx={{ px: 0, mr: 1, border: groupNumber == 5 ? '1px dashed #2196f3' : '1px dashed grey' }}>5</Button>    
    </Box>    
  );
}