'use strict';
const { FeeMarketEIP1559Transaction } = require('@ethereumjs/tx');
const Common = require('@ethereumjs/common').default;
const { Chain, Hardfork } = require('@ethereumjs/common');
import { formatRPCResponse, getCommon } from  './chainUtils'

function sendTxAction (web3,chain){

    //最大允许的gas,用作合约状态判断
    const maxAllowGasForCheck = 2000000
    const common = getCommon(chain)
    //入口
    async function _sendTx({from, to, value, gasLimit, maxPriorityFeePerGas, maxFeePerGas, data, nonce,pk}) {
        ////console.log('chain==>',chain)
        ////console.log({from, to, value, gasLimit, maxPriorityFeePerGas, maxFeePerGas, data, nonce})

        const estimate_Gas = await _estimateGas({from, to, value, gasLimit, maxPriorityFeePerGas, maxFeePerGas, data, nonce})
        ////console.log('estimate_Gas ==>',estimate_Gas)
        if (estimate_Gas.error) {
            return formatRPCResponse(estimate_Gas)
        }      

        if (typeof estimate_Gas == 'number' && estimate_Gas < maxAllowGasForCheck){                    
            const rawTx = await constructRawTx({from, to, value, gasLimit, maxPriorityFeePerGas, maxFeePerGas, data, nonce});                         
            const { response: signedTransaction, error } = await signTransaction(rawTx,pk);        
            if (error) {
                return { error };
            }                  
            const response = await sendTx({signedTransaction})        
            ////console.log('Transaction : ', response);
            // if(response.error){
            //    response = response
            // }        
            return response;        
        }

        return  { error : 'Unknow error found.' }; 

    }
    
    //构建交易raw
    async function constructRawTx({from, to, value, gasLimit, maxPriorityFeePerGas, maxFeePerGas, data, nonce}) {
        //console.log('gasLimit//',gasLimit)
        const rawTx = {
          from,            
          to,
          value: web3.utils.toHex(value*10**18),
          gasLimit: web3.utils.numberToHex(gasLimit),
          maxPriorityFeePerGas: web3.utils.toHex(maxPriorityFeePerGas*10**9),
          maxFeePerGas: web3.utils.toHex(maxFeePerGas*10**9),
          data: data,
          nonce: web3.utils.toHex(nonce),
          type: '0x2'
        };
        return rawTx;
    }
    
    //签名交易
    async function signTransaction(rawTx,pk) { 
        const pkey = Buffer.from(pk, 'hex');
        const tx = FeeMarketEIP1559Transaction.fromTxData(rawTx, { common });
        const signedTx = tx.sign(pkey);
        const serializedTx = '0x'+signedTx.serialize().toString('hex')
        //console.log(rawTx.from,serializedTx)
        return { response: serializedTx };
    }
    
    //发送交易
    async function sendTx({ signedTransaction }) {
        try {
            const response = await web3.eth.sendSignedTransaction(signedTransaction);
            ////console.log('response',response)
            return response
        } catch (error) {
            return formatRPCResponse(error)
        }
    }

    //估计交易gas
    //estimateGas async返回值是一个promise对象 await命令后面跟着的是一个promise对象
    async function _estimateGas({from, to, value, gasLimit, maxPriorityFeePerGas, maxFeePerGas, data, nonce}){
        try{
            const normalGas =  await web3.eth.estimateGas({
                from: from,
                to: to, 
                value: web3.utils.toHex(web3.utils.toWei(value.toString())),
                gasLimit: web3.utils.numberToHex(gasLimit),
                maxPriorityFeePerGas: web3.utils.toHex(maxPriorityFeePerGas*10**9),
                maxFeePerGas: web3.utils.toHex(maxFeePerGas*10**9),
                data: data
            });
            //没有开启mint会返回{error: 'Returned error: execution reverted'}
            //另外一些返回
            //{"jsonrpc":"2.0","id":5,"error":{"code":-32000,"message":"err: insufficient funds for gas * price + value: address 0xb0F0FA4F561C22c90225a1c557e69Ae764778d69 have 18166361458794181 want 20000000000000000 (supplied gas 210499)"}}
            //error.message==> Returned error: err: insufficient funds for gas * price + value: address 0xb0F0FA4F561C22c90225a1c557e69Ae764778d69 have 18166361458794181 want 20000000000000000 (supplied gas 210499)
            // error==> Error: Returned error: err: insufficient funds for gas * price + value: address 0xb0F0FA4F561C22c90225a1c557e69Ae764778d69 have 18166361458794181 want 20000000000000000 (supplied gas 160499)
            // at Object.ErrorResponse (errors.js:28:1)
            // at index.js:302:1
            // at request.onreadystatechange (index.js:98:1)

            //正常223097 0xd60800239864ff1066720a4cddd2cace1d6c1c39
            //Returned error: execution reverted
            return normalGas; 
        }catch(error){
            //error对象：{"code":-32000,"message":"err: insufficient funds for gas * price + value: address 0xb0F0FA4F561C22c90225a1c557e69Ae764778d69 have 18166361458794181 want 20000000000000000 (supplied gas 210499)"}
            ////console.log('_estimateGas error==>',error)
            ////console.log('typeoferror',typeof error)            
            return formatRPCResponse(error)
        }           
    }  
       
    return  { _sendTx, _estimateGas }

}

export { sendTxAction }