import React, {  useState, useEffect } from "react";
import {useTranslation} from 'react-i18next';
import Storage from 'helper/Storage'

import {Grid,Button,TextField,Alert} from '@mui/material';
import { styled } from '@mui/material/styles';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

import PkGroup from 'components/common/PkGroup';

const Input = styled('input')({
  display: 'none',
});

let groupNumber = 1

export default function ImportPrivateKey(props) {
  const { t } = useTranslation()
  const web3 = props.provider;
  let storagePk = Storage.get('privateKey')
  ?  
    (
    Storage.get('privateKey').length >  0 
      ? Storage.get('privateKey') 
      : [{
        addrStr:"",   
        pkStr:"",
        pkArr:[],
        addrArr:[]
        },{
        addrStr:"",   
        pkStr:"",
        pkArr:[],
        addrArr:[]
        },{
        addrStr:"",   
        pkStr:"",
        pkArr:[],
        addrArr:[]
        },{
        addrStr:"",   
        pkStr:"",
        pkArr:[],
        addrArr:[]
        },{
        addrStr:"",   
        pkStr:"",
        pkArr:[],
        addrArr:[]
        }
      ]
    )
  :[{
     addrStr:"",   
     pkStr:"",
     pkArr:[],
     addrArr:[]
    },{
     addrStr:"",   
     pkStr:"",
     pkArr:[],
     addrArr:[]
    },{
     addrStr:"",   
     pkStr:"",
     pkArr:[],
     addrArr:[]
    },{
     addrStr:"",   
     pkStr:"",
     pkArr:[],
     addrArr:[]
    },{
     addrStr:"",   
     pkStr:"",
     pkArr:[],
     addrArr:[]
    }
  ]
  //console.log('storagePk',storagePk)
  const [privateKey, setPrivateKey] = useState(storagePk[0]);    
  //console.log('privateKey',privateKey)
  //build data
  const initPrivateKey = ((privateKeyObject)=>{
    setPrivateKey(privateKeyObject);
    props.setPk(privateKeyObject)
  })

  const buildPrivateKey = ((pkStr)=>{
    let pkObject = {}
    const pattern = /[ABCDEFabcdef0-9]{64}/g;
    let pkArr = pkStr.match(pattern);
    pkObject.pkArr = pkArr ? pkArr : [];
    let addrArr = []
    if(pkArr){
      //console.log(pkArr)
      addrArr = pkArr.map((pKey)=>{
        //console.log('pKey',pKey)
        web3.eth.accounts.privateKeyToAccount(pKey).address
        //console.log('00000',web3.eth.accounts.privateKeyToAccount(pKey).address)
        return web3.eth.accounts.privateKeyToAccount(pKey).address
      })      
    }
    pkObject.addrArr = addrArr;  
    pkObject.pkStr = pkArr.join('\r\n');
    pkObject.addrStr = addrArr.join('\r\n'); 
    //console.log('pkObject',pkObject)                       
    setPrivateKey(pkObject);
    storagePk[groupNumber-1] = pkObject
    Storage.set('privateKey',[...storagePk])
    props.setPk(pkObject)
  })

  //choose file to import
  const inputFile =()=> {
    let pkFile = document.querySelector('#pKeyFile').files[0];
    if(pkFile){
      var reader = new FileReader();
      reader.readAsText(pkFile);
      reader.onload = function(){          
        buildPrivateKey(this.result);
        document.querySelector('#pKeyFile').value = ''//solute the same file can not be upload twice
      }
    }
  }  

  //button action
  const setPKeyAction = (event) => {
    buildPrivateKey(event.target.value); 
  }; 

  //'clear' button action
  const clearPKs =(()=>{
    try{
      let pkObject = {
        addrStr:"",
        pkStr:"",
        pkArr:[],
        addrArr:[] 
      }
      setPrivateKey(pkObject) 
      storagePk[groupNumber-1] = pkObject
      Storage.set('privateKey',[...storagePk])
      props.setPk(pkObject)   
      return
    }
    catch(e){
      Alert("unknow error")
      return
    }
  })

  const [showPK, setShowPK] = React.useState(true);
  const showPKChange = (event) => {
    setShowPK(event.target.checked);
  };
  
  const setGroupAction = (val) => {
    groupNumber = val    
    //console.log('[+] groupNumber ',groupNumber) 
    const pkObject = storagePk[groupNumber-1]
    setPrivateKey(pkObject) 
    props.setPk(pkObject)      
  }; 
  
  useEffect(() => {
    //console.log('storagePk',storagePk)
    initPrivateKey(storagePk[0]);
	}, []);

  return (  
    <Grid container spacing={2}>      
      <Grid item xs={12}>
        <Grid container spacing={1}>
          <Grid item xs={9}>
            <TextField              
              style = {{fontSize:8}}
              fullWidth
              id="pk"
              label={props.title + "(" +  privateKey.pkArr.length + ")"}
              multiline
              rows={6}
              value={showPK ? privateKey.pkStr : privateKey.addrStr}
              onChange={setPKeyAction}
              disabled = {true}
              error = {props.error}
          />
          </Grid> 
          <Grid item xs={3}>
            <Grid container spacing={2}>
              <Grid item xs={12}>
              <label htmlFor="pKeyFile">
                <Input accept="text/*" id="pKeyFile" multiple type="file" onChange={inputFile} />
                <Button variant="outlined" component="span">
                {t('importPrivateKey.import')}
                </Button>
              </label>
              </Grid>               
              <Grid item xs={12}>
                <Button 
                  variant="outlined" 
                  onClick={clearPKs}  
                  disabled = {privateKey.pkArr.length == 0}>{t('importPrivateKey.clear')}
                </Button>
              </Grid>  
              <Grid item xs={12}>
              <FormControlLabel
                label={t('importPrivateKey.showPrivateKey')}            
                control={
                  <Switch
                    checked={showPK}
                    onChange={showPKChange}
                    inputProps={{ 'aria-label': 'controlled' }}
                    size="small"
                  />
                }
              />
              </Grid>                                                                   
            </Grid>
          </Grid>                          
        </Grid>
      </Grid>
      <PkGroup data = {{setGroupAction:setGroupAction}} />
    </Grid>    
  );
}